<template>
  <table class="table table-bordered table-sm modal-table max-content">
    <thead class="thead-light max-content sticky">
      <template v-if="hasSearchBar">
        <div class="div-search p-1 pb-2 sticky">
          <slot name="thead-search"></slot>
        </div>
      </template>

      <tr :class="hasSearchBar ? 'sticky2' : 'sticky'">
        <div class="max-content" :class="hasSearchBar ? 'sticky2' : 'sticky'">
          <slot name="thead"></slot>
        </div>
      </tr>
    </thead>
    <tbody :style="maxHeight">
      <div class="max-content">
        <slot name="tbody"></slot>
      </div>
    </tbody>
  </table>
</template>

<script>
export default {
  name: "TableModal",
  props: {
    tableHeight: {
      type: Number,
      default: 450,
    },
    hasSearchBar: { type: Boolean, default: false },
  },
  computed: {
    maxHeight() {
      return "max-height: " + this.tableHeight + "px!important";
    },
  },
};
</script>

<style scoped>
.modal-table {
  /* overflow: hidden !important; */
  font-weight: normal !important;
  background-color: #e9ecef !important;
  margin: 0 !important;
}
.modal-table thead {
  background-color: #e9ecef !important;
  display: block !important;
}
.modal-table tbody {
  display: block;
  width: 100%;
  max-height: 450px;
  min-height: 18px;
  overflow-y: scroll;
  background-color: #fff;
}

.modal-table td,
.modal-table th {
  text-align: center;
  vertical-align: middle !important;
  margin: 0 !important;
  padding: 0 5px !important;
}
.modal-table th {
  background-color: #e9ecef !important;
  font-size: 11px !important;
  height: 18px !important;
  user-select: none;
}
.modal-table td {
  font-size: 10px !important;
  border-top: 0 !important;
  /* height: 22px !important; */
}
.modal-table input[type="checkbox"] {
  position: relative !important;
  border-width: 1px !important ;
  margin-left: 0 !important;
  margin-top: 4px !important;
  margin-bottom: auto !important;
  height: 13px !important;
  width: 13px !important;
  -webkit-box-shadow: 0px 0px 0px 0.005px #555;
  -moz-box-shadow: 0px 0px 0px 0.005px #555;
