<template>
  <div class="main-component row p-0">
    <PageTitle :title="'Dashboard'" />

    <div class="alert alert-light mt-1" role="alert">
      <p class="user-paragraph" style="">
        Ol {{ getUsuarioFirstName }}! Este  o sistema de gerenciamento de
        Plano Departamental do DCC - Departamento de Cincia da Computao.
        <br />Abaixo segue um resumo das funcionalidade de cada tela:
      </p>
      <div class="row">
        <LinksList v-if="Admin" :title="'PlANO'" :pages="linksPlanoOrdered" />
        <LinksList :title="'RELATRIOS'" :pages="linksRelatoriosOrdered" />
        <LinksList
          v-if="Admin"
          :title="'GERENCIAR'"
          :pages="linksGerenciarOrdered"
        />
      </div>
    </div>
  </div>
</template>

<script>
import _ from "lodash";
import { mapGetters } from "vuex";
import PageTitle from "@/components/PageTitle";
import LinksList from "./LinksList";

export default {
  name: "DashboardHome",
  components: { PageTitle, LinksList },
  data() {
    return {
      linksPlano: [
        {
          title: "Graduao - DCC",
          routerName: "pedidos",
          description: `Pgina onde encontra-se o cadastro de todas as turmas
            de todas as disciplinas oferecidas pelo DCC. Permite a alterao
            de vagas, horrios, sala e docentes de todas as disciplinas.`,
        },
        {
          title: "Graduao - Outros",
          routerName: "turmasExternas",
          description: `Similar a Tabela Interna, onde encontra-se o cadastro
            de todas as turmas de disciplinas oferecidas por outros departamentos
            (externos ao DCC), e que fazem parte da grade dos cursos cadastrados.`,
        },
        {
          title: "Ps Graduao",
          routerName: "cargaPos",
          description: `Tela onde localiza-se uma tabela com as cargas horrias
            de cada docente encarregado das bolsas de ps-graduao.`,
        },
        {
          title: "Validaes",
          routerName: "validacoes",
          description: `Tela onde localiza-se as validaes do Plano Departamental.`,
        },
      ],
      linksRelatorios: [
        {
          title: "Carga Professores",
          routerName: "cargaProfessores",
          description: `Listagem de todos professores e as disciplinas que estes
            ministram, alm das bolsas de ps-graduo. Mostra o somatrio
            parcial por semestre, e o total da carga horria de cada docente.`,
        },
        {
          title: "Grades Disciplinas",
          routerName: "gradeDisciplinas",
          description: `Listagem das disciplinas do DCC, divididas de acordo com seus
              perfis. Mostra, alm disso, quais semestres e perodos cada uma 
              ofertada nas grades dos cursos.`,
        },
        {
          title: "Horrios - Cursos",
          routerName: "horarios",
          description: `Pgina onde listam-se os horarios dos cursos do DCC, permitindo
            filtragem por semestres e cursos.`,
        },
        {
          title: "Horrios - Laboratrios",
          routerName: "laboratoriosAlocacao",
          description: `Lista as alocaes dos laboratrios em ambos os semestres.`,
        },
        {
          title: "Plano Departamental",
          routerName: "relatorioDisciplinas",
          description: `Lista as turmas, horrios e professores alocados por disciplina.`,
        },
      ],
      linksGenrenciar: [
        {
          title: "Cursos ",
          routerName: "cursos",
          description: `Lista todos os cursos presentes no sistemas (alm dos externos ao
            DCC), e permite a adio e alterao das informaes dos cursos.`,
        },
        {
          title: "Disciplinas ",
          routerName: "disciplinas",
          description: `Listagem de disciplinas de todos os departamentos cadastrados no
            sistema. Permite a alterao de nome, cdigo, carga terica e
            pratica, perfis e tambm define se  EAD ou no.`,
        },
        {
          title: "Disciplinas na Grade ",
          routerName: "gradeEdit",
          description: `Onde encontram-se as disciplinas de cada grade do DCC atuais e
            antigas que ainda esto em atividade, e permite a alterao ou
            adio de novas disciplinas.`,
        },
        {
          title: "Docentes ",
          routerName: "docentes",
          description: `Listagem de todos os docentes ativo e inativos do DCC, alm de
            permitir a alterao de seus perfis.`,
        },
        {
          title: "Grades ",
          routerName: "grades",
          description: `Onde encontram-se as grades dos cursos do DCC atuais e antigas que
            ainda esto em atividade, e permite a adio de novas grades.`,
        },
        {
          title: "Log ",
          routerName: "history",
          description: `Listagem de todas as modificaes feitas pelos usurios no
            sistema.`,
        },
        {
          title: "Perfis ",
          routerName: "perfis",
          description: `Onde encontram-se os perfis definidos para cada disciplina nas
            tabelas, permitindo tambm a adio e alterao de perfis, seus
            nomes e as cores destinadas a eles.`,
        },
        {
          title: "Salas ",
          routerName: "salas",
          description: `Listagem de todas as salas registradas no sistema. Permite a
            adio de novas salas e laboratrios.`,
        },
      ],
    };
  },
  created() {
    // console.log(this.$store);
  },
  computed: {
    linksPlanoOrdered() {
      return this.linksPlano;
    },
    linksRelatoriosOrdered() {
      return _.orderBy(this.linksRelatorios, "title");
    },
    linksGerenciarOrdered() {
      return _.orderBy(this.linksGenrenciar, "title");
    },
    Admin() {
      if (this.$store.state.auth.Usuario.admin === 1) {
        return true;
      } else {
        return false;
      }
    },
    ...mapGetters(["getUsuarioFirstName"]),
  },
};
</script>

<style scoped>
.alert {
  word-break: normal;
  text-align: justify;
  margin-left: 0;
  padding: 0 !important;
  margin-right: 1%;
  font-weight: normal;
  color: black;
  padding-right: 30px;
}
.user-paragraph {
  font-size: 12px;
}
</style>
